"use client";
import { useEffect, useState } from "react";
import { io } from "socket.io-client";

const socket = io("http://localhost:4000", {
  transports: ["websocket"],
});
interface Message {
    message : string ,
    room : string
  
}

export default function Home() {
  const [message, setMessage] = useState("");
  const [received, setReceived] = useState<Message[]>([]);

  useEffect(() => {
    // Example: community name from AI API
    const communityName = "community_123";

    // Tell server to join this room
    socket.emit("joinRoom", communityName);

    socket.on("receiveMessage", (data) => {
      setReceived((prev) => [...prev, data]);
    });

    socket.on("userJoined", (msg) => {
      console.log(msg);
    });

    return () => {
      socket.off("receiveMessage");
      socket.off("userJoined");
    };
  }, []);

  const sendMessage = () => {
    socket.emit("sendMessage", {
      room: "community_123",
      message,
    });
    setMessage("");
  };

  return (
    <div>
      <h1>Community Chat</h1>

      <input
        value={message}
        onChange={(e) => setMessage(e.target.value)}
        placeholder="type message"
      />
      <button onClick={sendMessage}>Send</button>

      {received.map((m:{message : string , room:string}, i) => (
        <p key={i}>{m.message}</p>
      ))}
    </div>
  );
}
